@import "./landing";
@import "./suppliers";
@import "./about";
@import "./contact";

section {
    &:not(#landing-section) {
        min-height: fit-content;
        margin: 5% !important;

        &:not(#about-section) {
            display: flex;
            flex-direction: column;
            align-items: center;
            border: 1px solid rgb(115, 115, 115);
            padding: 5%;
        }
    }
}

section {
    .section-title {
        font-size: 3rem;
    }

    .section-description {
        font-size: 2rem;
    }
}
